html{
    background-color: rgb(151, 95, 10);
}

div.App {
  height: 100%;
  background-color: rgb(0, 0, 0);
  color: aquamarine;
}

img.profile_pic{
  height: fit-content;
  width: fit-content;
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
}

div.home.container{
    background-color: rgb(6, 12, 99);
    background-image: url(https://www.njea.org/wp-content/uploads/2014/10/Coding.jpg);
    height: 100%;
}

.project_logo{
    height: 100%;

}
.project_logo:hover {
  opacity: 20%;
}

.contact_logo:hover {
  opacity: 20%;
}

button.linkedin:hover,
button.github:hover {
  background-color: darkgoldenrod;
  font-size: 20px;
  opacity: 700%;
  animation: mymove 2s infinite;
}
@keyframes mymove {
  from {
    background-color: red;
  }
  to {
    background-color: blue;
  }
}

.profile_pic{
    border-radius: 50%;
    width: 100%;
    max-width: 100%;
    height: auto;
   size: 30px;
    opacity: 50%;
}
